'use client'

import { useState, useEffect } from 'react'
import { Circle } from 'lucide-react'
import CameraSection from '@/components/CameraSection'
import HardwareControl from '@/components/control_hardware'
import GateInfo from '@/components/gate_condition'
import OperationRecord from '@/components/operator'

export default function SecurityCameraGrid() {
  const [activeCamera, setActiveCamera] = useState<number | null>(null)

  // Reset active camera after 10 seconds of inactivity
  useEffect(() => {
    if (activeCamera === null) return

    const timer = setTimeout(() => setActiveCamera(null), 10000)
    return () => clearTimeout(timer)
  }, [activeCamera])

  const activeCamerasCount = cameraFeeds.filter((cam) => cam.isActive).length

  return (
    <div className="min-h-screen rounded-lg bg-gray-100 p-4">
      {/* Header with camera status */}
      <header className="flex items-center justify-between mb-6 px-4 text-gray-700">
        <div className="flex items-center gap-2">
          <Circle className="w-2 h-2 fill-green-500 text-green-500 animate-pulse" />
          <span className="text-sm">{activeCamerasCount} Camera Active</span>
        </div>
      </header>

      {/* Main layout */}
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Camera section */}
        <div className="col-span-1 lg:col-span-2">
          <CameraSection activeCamera={activeCamera} setActiveCamera={setActiveCamera} />
        </div>

        {/* Control and info sections */}
        <div className="col-span-1 space-y-6">
          <HardwareControl
            onUp={() => console.log('Camera up')}
            onStop={() => console.log('Camera stop')}
            onDown={() => console.log('Camera down')}
          />
          <GateInfo />
          <OperationRecord />
        </div>
      </div>
    </div>
  )
}